import React from 'react'

const PersonForm = (props) => {    

    return (

     <form onSubmit={(e) => props.onSubmit(e)}>
          
        <div>
          Name: <input value={props.newName} type="text"
                       onChange={(e)=> props.handleChangeName(e.target.value)} required/>
        </div>
        
        <div>Number: <input value={props.newNumber} type="text"
                            onChange={(e) => props.handleChangeNumber(e.target.value)} required />
        </div>
        
        <div>
          <button type="submit" className="btn-success btn-block">Add</button>
        </div>
      </form>
    )
}

export default PersonForm