import React from 'react'

const Persons = ({persons,onClick}) => {   

    const rows = () => persons.map(person => {
        return(
           <tr key={person.name}> 
                <td>{person.name}</td>
                <td>{person.number}</td>
                <td><button className='btn btn-danger' onClick={()=>onClick(person)}>Delete</button></td>
           </tr>

        )
    });

    return(
        <table>
            <tbody>
              {rows()}  
            </tbody>            
        </table>
    )

}

export default Persons